import React from 'react'
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import ColorPicker from 'material-ui-color-picker'


const FillColor = (classes) => {

    let color = (localStorage.getItem('fillcolor') === null ? '#ffffff' : localStorage.getItem('fillcolor'));
    const vis = localStorage.getItem('vis') === null ? 'bars' : localStorage.getItem('vis');

    const setColor = (color) => {
        if (color !== undefined && localStorage.getItem('fillcolor') !== color) {
            localStorage.setItem('fillcolor', color);
        }
    };
    if (vis === 'multiColor' || vis === 'colorful_circle') {
        return (<></>);
    } else {
        return (
            <FormControl className={"all-form-inputs " + classes.formControl}>
                <InputLabel shrink id="demo-simple-select-placeholder-label-label">
                    Fill Color
                </InputLabel>
                <ColorPicker style={{marginTop: "18px"}}
                             name='color'
                             defaultValue={color}
                    // value={this.state.color} - for controlled component

                             onChange={setColor}

                />
            </FormControl>
        )
    }
};

export default FillColor;