import React from 'react'
import {Link} from "react-router-dom";
import Share from "./Share";

const Footer = () => {
    return (
        <div className="footer">
            <div className="share-container">
                <h2 style={{marginLeft: "32px"}}>Share Soundvisualiser</h2>
                <Share/>
            </div>
            <div className="f-container">
                <h3 style={{marginLeft: "0"}}>Resources</h3>
                <p className="footer-p">How to use (inactive)</p>
                <Link className="a-footer"
                      to="./main"><p className="footer-p">Remove footer</p></Link>
                <Link className="a-footer"
                      to="./visualizer"
                      target="_blank"><p className="footer-p">Fullscreen visualizer</p></Link>
            </div>
            <div className="f-container">
                <h3 style={{marginLeft: "0"}}>Add to Desktop</h3>
                <a className="a-footer"
                   href="https://natomasunified.org/kb/add-website-to-mobile-device-home-screen/"
                   target="_blank"><p className="footer-p">Mobile</p></a>
                <a className="a-footer"
                   href="https://support.mozilla.org/en-US/kb/create-desktop-shortcut-website"
                   target="_blank"><p className="footer-p">Desktop</p></a>
            </div>
            <div className="f-container">
                <h3 style={{marginLeft: "0"}}>Community</h3>
                <a className="a-footer"
                   href="https://reddit.com/r/SoundVisualization"
                   target="_blank"><p className="footer-p">Reddit</p></a>
            </div>
        </div>
    )
};

export default Footer;