import React from 'react'
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import ColorPicker from 'material-ui-color-picker'


const LineColor = (classes) => {

    let color = (localStorage.getItem('linecolor') === null ? '#ffffff' : localStorage.getItem('linecolor'));

    const setColor = (color) => {
        if (color !== undefined && localStorage.getItem('linecolor') !== color) {
            localStorage.setItem('linecolor', color);
        }
    };

    return (
        <FormControl className={"all-form-inputs " + classes.formControl}>
            <InputLabel shrink id="demo-simple-select-placeholder-label-label">
                Line Color
            </InputLabel>
            <ColorPicker style={{marginTop: "18px"}}
                         name='color'
                         defaultValue={color}
                // value={this.state.color} - for controlled component

                         onChange={setColor}

            />
        </FormControl>
    )
};

export default LineColor;