import React from 'react';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import Slider from '@material-ui/core/Slider';


const Clipping = (classes) => {


    const handleClipping = (value) => {
        if (localStorage.getItem('clipping') != value) {
            localStorage.setItem('clipping', value);
        }
    }

    const marks = [
        {
            value: 0,
            label: '0',
        },
        {
            value: 100,
            label: '100',
        },
    ];

    return (
        <FormControl className={"all-form-inputs " + classes.formControl}>
            <InputLabel shrink id="demo-simple-select-placeholder-label-label">
                Clipping
            </InputLabel>
            <Slider style={{marginTop: "24px"}}
                    getAriaValueText={handleClipping}
                    aria-labelledby="discrete-slider"
                    valueLabelDisplay="auto"
                    step={1}
                    marks={marks}
                    min={0}
                    max={100}
                    defaultValue={localStorage.getItem('clipping') === null ? 20 : localStorage.getItem('clipping')}
            />
        </FormControl>
    );
};


export default Clipping;